*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}
.active_class{
  color: aquamarine;
}
span{
  color: aqua;
}
.btn{
  width: 150px;
  border-radius: 50px;
  padding: 10px 30px;
  background-color :blue !important;
  color:#fff !important;
  font-size: 16px;
  text-align: center;
  border: 2px solid #fff;
  transition: 0.5s ease;

}
.btn:hover{
  background-color :aqua  !important;
  color: #fff;
  transition: 0.5s ease  !important;
 
}

.active_class:hover{
  color: aqua;
  border-bottom: 2px solid purple;
  

}

.home_section{
  width: 100vw;
  height: 100vh;
  background-image:url(https://images.pexels.com/photos/3848197/pexels-photo-3848197.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940);
  background-position: center;
  background-size: cover;
  
}
.main_div{
  display: flex;
  justify-content: center;
  align-items: center;
  
  padding-top: 300px;
}

.center_div {
 text-align: center;
 color: #fff;
 flex-direction: column;
 
}
.center_div h1{
  font-size: 70px;
  font-weight: 700;
}



.bg_color{
  background-color:rgba(0,0,0,.6);
}
.about_section{
  position: relative;

}
.about_section::before{
  content: '';
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  background: linear-gradient(330deg,aqua 50%,#fff 0%);
  z-index: -1;
}


.Service_section{
  padding: 100px  0;
  margin: 100px  0;
  
}

